'use strict';

/**
* Return the index of the first row in an input matrix which has the same elements as a provided search vector.
*
* @module @stdlib/blas/ext/base/gindex-of-row
*
* @example
* import gindexOfRow from '@stdlib/blas/ext/base/gindex-of-row';
*
* var A = [ 1.0, 2.0, 3.0, 4.0, 0.0, 0.0 ]; // => [ [ 1.0, 2.0 ], [ 3.0, 4.0 ], [ 0.0, 0.0 ] ]
* var x = [ 3.0, 4.0 ];
*
* var out = gindexOfRow( 'row-major', 3, 2, A, 2, x, 1 );
* // returns 1
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-nonenumerable-read-only-property';
import main from './main.js';
import ndarray from './ndarray.js';


// MAIN //

setReadOnly( main, 'ndarray', ndarray );


// EXPORTS //

export default main;
