'use strict';

/**
* Return the last index of a specified search element in a strided array.
*
* @module @stdlib/blas/ext/base/glast-index-of
*
* @example
* import glastIndexOf from '@stdlib/blas/ext/base/glast-index-of';
*
* var x = [ -2.0, 1.0, 3.0, -5.0, 4.0, 0.0, -1.0, 3.0 ];
*
* var idx = glastIndexOf( x.length, 3.0, x, 1 );
* // returns 7
*
* @example
* import glastIndexOf from '@stdlib/blas/ext/base/glast-index-of';
*
* var x = [ -2.0, 1.0, 3.0, -5.0, 4.0, 0.0, -1.0, 3.0 ];
*
* var idx = glastIndexOf.ndarray( x.length, 3.0, x, 1, 0 );
* // returns 7
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-nonenumerable-read-only-property';
import main from './main.js';
import ndarray from './ndarray.js';


// MAIN //

setReadOnly( main, 'ndarray', ndarray );


// EXPORTS //

export default main;
