'use strict';

// MODULES //

import isRowMajor from '@stdlib/ndarray/base/assert/is-row-major';


// MAIN //

/**
* Performs the symmetric rank 1 operation `A = *x*x^T + A` where `` is a scalar, `x` is an `N` element vector, and `A` is an `N` by `N` symmetric matrix.
*
* @private
* @param {string} uplo - specifies whether the upper or lower triangular part of the symmetric matrix `A` should be referenced
* @param {NonNegativeInteger} N - number of columns in the matrix `A`
* @param {number} alpha - scalar constant
* @param {Object} x - input vector object
* @param {Collection} x.data - input vector data
* @param {Array<Function>} x.accessors - array element accessors
* @param {integer} strideX - stride length for `x`
* @param {NonNegativeInteger} offsetX - starting index for `x`
* @param {Object} A - input matrix object
* @param {Collection} A.data - input matrix data
* @param {Array<Function>} A.accessors - array element accessors
* @param {integer} strideA1 - stride of the first dimension of `A`
* @param {integer} strideA2 - stride of the second dimension of `A`
* @param {NonNegativeInteger} offsetA - starting index for `A`
* @returns {Object} input matrix object
*
* @example
* import toAccessorArray from '@stdlib/array/base/to-accessor-array';
* import arraylike2object from '@stdlib/array/base/arraylike2object';
*
* var A = [ 1.0, 2.0, 3.0, 2.0, 1.0, 2.0, 3.0, 2.0, 1.0 ]; // => [ [ 1.0, 2.0, 3.0 ], [ 2.0, 1.0, 2.0 ], [ 3.0, 2.0, 1.0 ] ]
* var x = [ 1.0, 2.0, 3.0 ];
*
* gsyr( 'upper', 3, 1.0, arraylike2object( toAccessorArray( x ) ), 1, 0, arraylike2object( toAccessorArray( A ) ), 3, 1, 0 );
* // A => [ 2.0, 4.0, 6.0, 2.0, 5.0, 8.0, 3.0, 2.0, 10.0 ]
*/
function gsyr( uplo, N, alpha, x, strideX, offsetX, A, strideA1, strideA2, offsetA ) { // eslint-disable-line max-len
	var getX;
	var getA;
	var setA;
	var xbuf;
	var Abuf;
	var isrm;
	var tmp;
	var ix0;
	var ix1;
	var sa0;
	var sa1;
	var i0;
	var i1;
	var ia;
	var ox;
	var v;

	// Cache references to array data:
	xbuf = x.data;
	Abuf = A.data;

	// Cache references to element accessors:
	getX = x.accessors[ 0 ];
	getA = A.accessors[ 0 ];
	setA = A.accessors[ 1 ];

	isrm = isRowMajor( [ strideA1, strideA2 ] );
	if ( isrm ) {
		// For row-major matrices, the last dimension has the fastest changing index...
		sa0 = strideA2; // stride for innermost loop
		sa1 = strideA1; // stride for outermost loop
	} else { // isColMajor
		// For column-major matrices, the first dimension has the fastest changing index...
		sa0 = strideA1; // stride for innermost loop
		sa1 = strideA2; // stride for outermost loop
	}
	ox = offsetX;
	if (
		( !isrm && uplo === 'upper' ) ||
		( isrm && uplo === 'lower' )
	) {
		ix1 = ox;
		for ( i1 = 0; i1 < N; i1++ ) {
			v = getX( xbuf, ix1 );
			if ( v !== 0.0 ) {
				tmp = alpha * v;
				ia = offsetA + (sa1*i1);
				ix0 = ox;
				for ( i0 = 0; i0 <= i1; i0++ ) {
					v = getX( xbuf, ix0 ) * tmp;
					setA( Abuf, ia, getA( Abuf, ia ) + v );
					ix0 += strideX;
					ia += sa0;
				}
			}
			ix1 += strideX;
		}
		return A;
	}
	// ( isrm && uplo === 'upper' ) || ( !isrm && uplo === 'lower' )
	ix1 = ox;
	for ( i1 = 0; i1 < N; i1++ ) {
		v = getX( xbuf, ix1 );
		if ( v !== 0.0 ) {
			tmp = alpha * v;
			ia = offsetA + (sa1*i1) + (sa0*i1);
			ix0 = ix1;
			for ( i0 = i1; i0 < N; i0++ ) {
				v = getX( xbuf, ix0 ) * tmp;
				setA( Abuf, ia, getA( Abuf, ia ) + v );
				ix0 += strideX;
				ia += sa0;
			}
		}
		ix1 += strideX;
	}
	return A;
}


// EXPORTS //

export default gsyr;
