'use strict';

/**
* Return the last index of a specified search element along an ndarray dimension.
*
* @module @stdlib/blas/ext/last-index-of
*
* @example
* import Float64Array from '@stdlib/array/float64';
* import ndarray2array from '@stdlib/ndarray/to-array';
* import ndarray from '@stdlib/ndarray/ctor';
* import lastIndexOf from '@stdlib/blas/ext/last-index-of';
*
* // Create a data buffer:
* var xbuf = new Float64Array( [ 1.0, 2.0, -3.0, 2.0, -5.0, 6.0 ] );
*
* // Define the shape of the input array:
* var sh = [ 2, 3 ];
*
* // Define the array strides:
* var sx = [ 3, 1 ];
*
* // Define the index offset:
* var ox = 0;
*
* // Create an input ndarray:
* var x = new ndarray( 'float64', xbuf, sh, sx, ox, 'row-major' );
*
* // Perform operation:
* var out = lastIndexOf( x, 2.0 );
* // returns <ndarray>
*
* var arr = ndarray2array( out );
* // returns [ 1, 0 ]
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-nonenumerable-read-only-property';
import main from './main.js';
import assign from './assign.js';


// MAIN //

setReadOnly( main, 'assign', assign );


// EXPORTS //

export default main;

// exports: { "assign": "main.assign" }
