'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-read-only-property';


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name base
* @memberof ns
* @readonly
* @type {Namespace}
* @see {@link module:@stdlib/blas/ext/base}
*/
import base from './../../ext/base';
setReadOnly( ns, 'base', base );

/**
* @name cusum
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/blas/ext/cusum}
*/
import cusum from './../../ext/cusum';
setReadOnly( ns, 'cusum', cusum );

/**
* @name findIndex
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/blas/ext/find-index}
*/
import findIndex from './../../ext/find-index';
setReadOnly( ns, 'findIndex', findIndex );

/**
* @name findLastIndex
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/blas/ext/find-last-index}
*/
import findLastIndex from './../../ext/find-last-index';
setReadOnly( ns, 'findLastIndex', findLastIndex );

/**
* @name indexOf
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/blas/ext/index-of}
*/
import indexOf from './../../ext/index-of';
setReadOnly( ns, 'indexOf', indexOf );

/**
* @name lastIndexOf
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/blas/ext/last-index-of}
*/
import lastIndexOf from './../../ext/last-index-of';
setReadOnly( ns, 'lastIndexOf', lastIndexOf );

/**
* @name linspace
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/blas/ext/linspace}
*/
import linspace from './../../ext/linspace';
setReadOnly( ns, 'linspace', linspace );

/**
* @name sorthp
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/blas/ext/sorthp}
*/
import sorthp from './../../ext/sorthp';
setReadOnly( ns, 'sorthp', sorthp );

/**
* @name sum
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/blas/ext/sum}
*/
import sum from './../../ext/sum';
setReadOnly( ns, 'sum', sum );

/**
* @name toSortedhp
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/blas/ext/to-sortedhp}
*/
import toSortedhp from './../../ext/to-sortedhp';
setReadOnly( ns, 'toSortedhp', toSortedhp );


// EXPORTS //

export default ns;
