'use strict';

/**
* BLAS level 2 routine to perform the symmetric rank 1 operation `A = *x*x^T + A` where `` is a scalar, `x` is an `N` element vector, and `A` is an `N` by `N` symmetric matrix.
*
* @module @stdlib/blas/base/gsyr
*
* @example
* import gsyr from '@stdlib/blas/base/gsyr';
*
* var A = [ 1.0, 2.0, 3.0, 2.0, 1.0, 2.0, 3.0, 2.0, 1.0 ]; // => [ [ 1.0, 2.0, 3.0 ], [ 2.0, 1.0, 2.0 ], [ 3.0, 2.0, 1.0 ] ]
* var x = [ 1.0, 2.0, 3.0 ];
*
* gsyr( 'row-major', 'upper', 3, 1.0, x, 1, A, 3 );
* // A => [ 2.0, 4.0, 6.0, 2.0, 5.0, 8.0, 3.0, 2.0, 10.0 ]
*
* @example
* import gsyr from '@stdlib/blas/base/gsyr';
*
* var A = [ 1.0, 2.0, 3.0, 2.0, 1.0, 2.0, 3.0, 2.0, 1.0 ]; // => [ [ 1.0, 2.0, 3.0 ], [ 2.0, 1.0, 2.0 ], [ 3.0, 2.0, 1.0 ] ]
* var x = [ 1.0, 2.0, 3.0 ];
*
* gsyr.ndarray( 'upper', 3, 1.0, x, 1, 0, A, 3, 1, 0 );
* // A => [ 2.0, 4.0, 6.0, 2.0, 5.0, 8.0, 3.0, 2.0, 10.0 ]
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-nonenumerable-read-only-property';
import main from './main.js';
import ndarray from './ndarray.js';


// MAIN //

setReadOnly( main, 'ndarray', ndarray );


// EXPORTS //

export default main;

// exports: { "ndarray": "main.ndarray" }
